@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap";
:root {
  font-synthesis: none;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  --black: #04152d;
  --black2: #041226;
  --black3: #020c1b;
  --black-lighter: #1c4b91;
  --black-light: #173d77;
  --pink: #da2f68;
  --orange: #f89e00;
  --gradient: linear-gradient(98.37deg, #f80000 0.99%, #da2f68 100%);
  font-family: Inter, Avenir, Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.error {
  color: red;
}

::-webkit-scrollbar {
  display: none;
}

.skeleton {
  background-color: #0a2955;
  position: relative;
  overflow: hidden;
}

.skeleton:after {
  content: "";
  background-image: linear-gradient(90deg, #19376300 0, #19376333 20%, #19376380 60%, #19376300);
  animation: 2s infinite shimmer;
  position: absolute;
  inset: 0;
  transform: translateX(-100%);
}

@keyframes shimmer {
  100% {
    transform: translateX(100%);
  }
}

.carouselSection {
  margin-bottom: 70px;
  position: relative;
}

.carouselSection > .contentWrapper {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.carouselSection .carouselTitle {
  color: #111010;
  margin-top: 1rem;
  font-size: 24px;
  font-weight: normal;
}

.homeBanner {
  background-color: #fff;
  align-items: center;
  width: 100%;
  height: 450px;
  display: flex;
  position: relative;
}

@media only screen and (width >= 768px) {
  .homeBanner {
    height: 700px;
  }
}

.homeBanner .Background-img {
  opacity: 1;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.homeBanner .Background-img:before {
  content: "";
  background-color: #00000080;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.homeBanner .Background-img img {
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
}

.homeBanner .opacity-layer {
  background: linear-gradient(#04152d00 0%, #04152d 79.17%);
  width: 100%;
  height: 250px;
  position: absolute;
  bottom: 0;
  left: 0;
}

@media only screen and (width >= 640px) {
  .homeBanner .contentWrapper {
    justify-content: center;
    align-items: center;
    width: 100%;
    display: flex;
  }
}

.homeBanner .contentWrapper .homeDetailContainer {
  color: #fff;
  text-align: center;
  flex-direction: column;
  align-items: center;
  max-width: 600px;
  display: flex;
  position: relative;
}

@media only screen and (width >= 640px) {
  .homeBanner .contentWrapper .homeDetailContainer {
    color: #000;
  }
}

.homeBanner .contentWrapper .homeDetailContainer .title {
  color: #fff;
  margin-bottom: 10px;
  font-size: 30px;
  font-weight: 500;
}

@media only screen and (width >= 768px) {
  .homeBanner .contentWrapper .homeDetailContainer .title {
    margin-bottom: 0;
    font-size: 60px;
  }
}

.homeBanner .contentWrapper .homeDetailContainer .subTitle {
  color: #fff;
  margin-bottom: 40px;
  font-size: 25px;
  font-weight: 500;
}

@media only screen and (width >= 640px) {
  .homeBanner .contentWrapper .homeDetailContainer .subTitle {
    font-size: 20px;
  }
}

@media only screen and (width >= 768px) {
  .homeBanner .contentWrapper .homeDetailContainer .subTitle {
    font-size: 24px;
  }
}

.homeBanner .contentWrapper .homeDetailContainer .searchInput {
  align-items: center;
  width: 80%;
  display: flex;
}

@media only screen and (width >= 1024px) {
  .homeBanner .contentWrapper .homeDetailContainer .searchInput {
    width: 100%;
  }
}

.homeBanner .contentWrapper .homeDetailContainer .searchInput label {
  display: none;
}

.homeBanner .contentWrapper .homeDetailContainer .searchInput input {
  background-color: #eedede;
  border: 0;
  border-radius: 30px 0 0 30px;
  outline: 0;
  width: calc(100% - 100px);
  height: 50px;
  padding: 0 15px;
  font-size: 14px;
}

@media only screen and (width >= 768px) {
  .homeBanner .contentWrapper .homeDetailContainer .searchInput input {
    font-family: 20px;
    width: calc(100% - 150px);
    height: 60px;
    padding: 0 30px;
  }
}

.homeBanner .contentWrapper .homeDetailContainer .searchInput button {
  background: var(--gradient);
  color: #fff;
  cursor: pointer;
  border-radius: 0 30px 30px 0;
  outline: 0;
  width: 100px;
  height: 50px;
  font-size: 10px;
}

@media only screen and (width >= 768px) {
  .homeBanner .contentWrapper .homeDetailContainer .searchInput button {
    width: 150px;
    height: 60px;
    font-size: 30px;
  }
}

.homeBanner .contentWrapper .homeDetailContainer .slider {
  flex-direction: column;
  align-items: flex-start;
  width: 50%;
  display: flex;
}

.homeBanner .contentWrapper .homeDetailContainer .slider h2 {
  text-transform: capitalize;
}

.contentWrapper {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.carousel {
  margin-bottom: 50px;
  font-family: Arial, sans-serif;
}

.carousel .contentWrapper {
  position: relative;
}

.carousel .arrow {
  color: #333;
  cursor: pointer;
  opacity: .5;
  z-index: 1;
  font-size: 30px;
  display: none;
  position: absolute;
  top: 44%;
  transform: translateY(-50%);
}

@media only screen and (width >= 768px) {
  .carousel .arrow {
    background: none;
    border: none;
    display: block;
  }
}

.carousel .arrow:hover {
  opacity: 1;
  transition: opacity .3s, transform .3s;
}

.carousel .carouselLeftNav {
  left: 30px;
}

.carousel .carouselRighttNav {
  right: 0;
}

.carousel .loadingSkeleton {
  text-align: center;
  gap: 10px;
  margin-left: -20px;
  margin-right: -20px;
  padding: 0 20px;
  display: flex;
  overflow-y: hidden;
}

@media only screen and (width >= 768px) {
  .carousel .loadingSkeleton {
    gap: 20px;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }
}

.carousel .loadingSkeleton .skeletonItem {
  flex-shrink: 0;
  width: 125px;
  height: 300px;
}

@media only screen and (width >= 768px) {
  .carousel .loadingSkeleton .skeletonItem {
    width: calc(25% - 15px);
  }
}

@media only screen and (width >= 1024px) {
  .carousel .loadingSkeleton .skeletonItem {
    width: calc(20% - 16px);
  }
}

.carousel .loadingSkeleton .skeletonItem .thumbnailImg {
  border-radius: 12px;
  width: 100%;
  height: 200px;
  margin-bottom: 30px;
  transition: transform .3s ease-in-out;
  position: relative;
}

.carousel .loadingSkeleton .skeletonItem .thumbnailImg:hover {
  transform: scale(1.1);
}

.carousel .loadingSkeleton .skeletonItem .title {
  width: 100%;
  height: 20px;
  margin-bottom: 10px;
}

.carousel .loadingSkeleton .skeletonItem .discount, .carousel .loadingSkeleton .skeletonItem .ratting {
  width: 30%;
  height: 20px;
}

.carousel .carouselItems {
  align-items: stretch;
  gap: 10px;
  margin-left: -20px;
  margin-right: -20px;
  padding: 0 20px;
  transition: transform .4s ease-in-out;
  display: flex;
  overflow-y: hidden;
}

@media (width >= 768px) {
  .carousel .carouselItems {
    gap: 20px;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }
}

.carousel .carouselItems .carouselItem {
  cursor: pointer;
  text-align: center;
  border-radius: 10px;
  flex-shrink: 0;
  width: 190px;
  margin: 20px 0;
  padding: 20px;
  font-family: Roboto, sans-serif;
  transition: transform .4s ease-in-out, box-shadow .3s ease-in-out;
  box-shadow: 0 4px 10px #0000001a;
}

@media (width >= 768px) {
  .carousel .carouselItems .carouselItem {
    width: calc(25% - 15px);
  }
}

@media (width >= 992px) {
  .carousel .carouselItems .carouselItem {
    width: calc(20% - 16px);
  }
}

.carousel .carouselItems .carouselItem .thumbnailImg {
  object-fit: cover;
  border-radius: 10px;
  width: 100%;
  height: 200px;
  transition: transform .3s ease-in-out;
}

.carousel .carouselItems .carouselItem .thumbnailImg:hover {
  transform: scale(1.05);
}

.carousel .carouselItems .carouselItem .title {
  color: #444;
  margin-top: 15px;
  font-size: 18px;
  font-weight: bold;
  line-height: 1.2;
}

.carousel .carouselItems .carouselItem .discount {
  color: #2980b9;
  margin-top: 10px;
  font-size: 16px;
  font-weight: bold;
}

.carousel .carouselItems .carouselItem .discount:hover {
  color: #409ad5;
}

.carousel .carouselItems .carouselItem .ratting {
  color: #fff;
  background: linear-gradient(90deg, #2980b9, #27ae60);
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 70%;
  height: 30px;
  margin-top: 15px;
  font-size: 14px;
  font-weight: bold;
  display: flex;
}

.carousel .carouselItems .carouselItem .ratting:hover {
  background: linear-gradient(90deg, #2e8ece, #2cc36b);
}

.carousel .carouselItems .carouselItem:hover {
  transform: scale(1.02);
  box-shadow: 0 8px 20px #0003;
}

.lazy-load-image-background.blur {
  filter: blur(15px);
}

.lazy-load-image-background.blur.lazy-load-image-loaded {
  filter: blur();
  transition: filter .3s;
}

.lazy-load-image-background.blur > img {
  opacity: 0;
}

.lazy-load-image-background.blur.lazy-load-image-loaded > img {
  opacity: 1;
  transition: opacity .3s;
}

.card-container {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: flex;
}

.card {
  cursor: pointer;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 200px;
  margin: 10px;
  padding: 10px;
  transition: transform .3s;
  box-shadow: 0 4px 8px #0000001a;
}

.card:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px #0003;
}

.card .thumbnailImg {
  object-fit: cover;
  border-radius: 5px;
  width: 100%;
  height: 150px;
  margin-bottom: 10px;
}

.card .card-body {
  flex-direction: column;
  display: flex;
}

.card .title {
